.App{
  margin: auto;
  width: 80%;
  padding: 10px;
}

.big-text{
  font-size: 25px;
  font-weight: bold;
}

.medium-text{
  font-size: 20px;
  font-weight: bold;
}

.item1 i{
  margin:4px;
}

.shef-img{
  width:90px;
  border-radius: 100%;
  align-items: center;
}

.item1 { grid-area: header; }
.item2 { grid-area: menu; border-top: 1px solid #333; word-wrap: normal;
  word-break:break-all; }
.item3 { grid-area: main; border-top: 1px solid #333; }
.item4 { grid-area: right; text-align: right;}
.item5 { grid-area: footer; border-top: 1px solid #333; padding:10px; margin-top:20px}

.grid-container {
  display: grid;
  grid-template-areas:
    'header header header header header right'
    'menu main main main main main'
    'footer footer footer footer footer footer';
  padding: 10px;
}

.grid-container > div {
  padding: 10px;
}

.sidebar{
  width: 250px;
}

.tag span{
  margin:3px;
  padding:2px 6px;
  cursor:pointer;
  line-height:35px;
}

