.projects li{
  border:1px solid #444;
  padding:0px 15px 15px 15px;
  margin:10px 0px;
  border-radius:10px;
}

.projects li:hover{
  box-shadow: 0px 0px 10px #BCFFB9 ;
  cursor:pointer;
}
.projects li h4 i{
